<template>
   <div class="item-list">
      <div class="item-list-head">
         <div class="number">序号</div>
         <div class="title">标题</div>
         <div class="releaser">发布人</div>
         <div class="releaseTime">发布时间</div>
         <div class="operate">操作</div>
      </div>
      <div class="item-list-body" >
         <div class="item-list-content" v-for="(ele, index) in $store.state.msg" :key='index' v-if="ele">
            <div class="number">{{index+1}}</div>
            <div class="title">{{ele.title}}</div>
            <div class="releaser">{{ele.releaser}}</div>
            <div class="releaseTime">{{ele.releaseTime}}</div>
            <div class="operate" v-if="ele">
               <span class="change" @click="toggleShow">修改
               </span>
               <span class="del" @click="decrease(index)">删除</span>
            </div>
            <decrease v-if="$store.state.isShow" :title='ele.title' :index='index'
                  :releaser='ele.releaser' :releaseTime="ele.releaseTime"></decrease>
         </div>
      </div>
   </div>
</template>

<script>
import {mapMutations} from 'vuex'
import decrease from './decrease'
export default {
   name: 'ItemList',
   data () {
      return {

      }
   },
   methods: {
      ...mapMutations([
         'add', 'decrease', 'change','toggleShow'
      ]),
      del () {

      }
   },
   components: {
      decrease
   }
}
</script>

<style scoped>
.item-list {
   /* border: 1px solid #eee; */
   margin-top: 8px;
   font-size: 15px;
}
.item-list .item-list-head,
.item-list .item-list-body .item-list-content {
   display: flex;
   font-weight: bold;
   background: #eee;
}
.item-list .item-list-body .item-list-content{
   background: none;
}
.item-list .item-list-head div,
.item-list .item-list-body .item-list-content div {
   padding: 20px 10px;
   border-bottom: 1px solid #eee;
   border-right: 1px solid #eee;
}
.number {
   width: 5%;
   border-left: 1px solid #eee;
}
.title {
   flex: 1;
}
.releaser {
   width: 15%;
}
.releaseTime {
   width: 20%;
}
.operate {
   width: 15%;
   display: flex;
   justify-content: space-between;
}
.operate span {
   cursor: pointer;
}
.operate .del {
   color: red;
}
.operate .change {
   color: #008cd5;
}
</style>



