<template>
   <div class="add-item">
      <input type="text" v-model="title" class="title" placeholder="标题">
      <input type="text" v-model="releaser" class="releaser" placeholder='发布人'>
      <input type="date" v-model="releaseTime" class="releaseTime" placeholder="发布时间">
      <button @click="add">新增</button>
   </div>
</template>

<script>
export default {
   name: 'AddItem',
   data () {
      return {
         title: '',
         releaser: '',
         releaseTime: ''
      }
   },
   methods: {
      add () {
         if(!this.check()){
            alert("填完再提交，傻吊！")
            return;
         }
         var msg = {
            title: this.title,
            releaser: this.releaser,
            releaseTime: this.releaseTime
         }
         this.$store.commit('add', msg);
         this.init();
      },
      init () {
         this.title = '';
         this.releaser = '';
         this.releaseTime = '';
      },
      check () {
         return this.title && this.releaser && this.releaseTime;
      }
   }

}
</script>

<style>
.add-item{
   height: 32px;
   border: 1px solid #eee;
   padding: 15px;
   display: flex;
   align-items: center;
}
.add-item input{
   padding: 5px;
   margin-right: 15px;
   border-radius: 3px;
   border: 1px solid #eee;
   font-size: 12px;
}
.add-item button {
   font-size: 12px;
   border: 0;
   background-color: #008cd5;
   color: #fff;
   padding: 4px 15px;
   border-radius: 3px;
}
</style>



