import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

var store =new Vuex.Store({
   state: {
     msg : [
       {
         title: '在移动设备上开发',
         releaser: '张韶涵',
         releaseTime: '2018-02-09'
       }
     ],
     isShow: false
   },
   mutations: {
      add (state, item) {
         state.msg.push(item);
      },
      decrease (state, itemIndex) {
        state.msg.splice(itemIndex, 1)
      },
      change (state, payload) {
        console.log('newValue',payload)
        var index = payload.index;
        var newValue = payload.msg;
        for (var value in newValue) {
          state.msg[index][value] = newValue[value];
        }
        console.log(state.msg[index])
      },
      toggleShow (state) {
        state.isShow = !state.isShow;
      }
   }
 })


export default store;


