<template>
   <div class="decrease">
      <div class="box">
         <div class="box-head">
            <span>修改</span>
            <span @click="toggleShow" class="cancel">X</span>
         </div>
         <div class="box-content">
            <input type="text" placeholder="标题" v-model="newTitle">
            <input type="text" placeholder="发布人" v-model="newReleaser">
            <input type="date" placeholder="发布时间" v-model="newReleaseTime">
            <div class="btn">
               <button @click="update">更新</button>
               <button @click="toggleShow">取消</button>
            </div>
         </div>
      </div>   
   </div>

</template>

<script>
import {mapMutations} from 'vuex'
export default {
   name: 'decrease',
   props: ['title', 'releaser', 'releaseTime','index'],
   // created () {
   //    newTitle: this.title,
   //    newReleaser: this.releaser,
   //    newReleaseTime = this.releaseTime
   // },
   data () {
      return {
         newTitle: this.title,
         newReleaser: this.releaser,
         newReleaseTime: this.releaseTime
      }
   },
   methods: {
      ...mapMutations([
         'toggleShow','change'
      ]),
      update () {
         var msg = {
            title: this.newTitle,
            releaser: this.newReleaser,
            releaseTime: this.newReleaseTime
         }
         var payload = {
            index: this.index,
            msg: msg
         }
         console.log(payload)
         this.change(payload);
         this.toggleShow();
      }
   }
}
</script>


<style scoped>
   .decrease {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      /* width: 100%;
      height: 100%; */
      background: rgba(0,0,0, 0.3);
      display: flex;
      justify-content: center;
      align-items: center;
   }
   .decrease .box {
      width: 300px;
      height: 250px;
      display: flex;
      flex-direction: column;
      background: #fff;
      border-radius: 3px;
      color: black;
      font-weight: 300;
   } 
   .decrease .box .box-head{
      position: relative;
      padding: 5px 10px;
      height: 21px;
      border-bottom: 1px solid #eee;
      line-height: 21px;
      font-size: 14px;
      font-weight: 500;
   }
   .decrease .box .box-head .cancel {
      float: right;
      cursor: pointer;
   }
   .decrease .box .box-content{
      display: flex;
      flex-direction: column;
      padding: 10px;
   }
   .decrease .box .box-content input,
   .decrease .box .box-content .btn{
      margin-top: 8px;
      font-size: 10px;
   }
   .decrease .box .box-content input{
      border-radius: 3px;
      padding: 3px 5px;
      border: 1px solid #eee;
   }
   .decrease .box .box-content .btn{
      display: flex;
      justify-content: space-around;
   }
   .decrease .box .box-content .btn button {
      border-radius: 3px;
      border: 1px solid #eee;
      background: #008cd5;
      color: #fff;
      padding: 4px 15px;
      font-size: 10px;
   }
</style>



